          box-sizing: border-box;
}

body {
  background: rgb(244, 243, 243);
  font-family: "Lato", sans-serif;
}

.wrapper {
  max-width: 480px;
  margin: auto;
  background: #fff;
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 50px;
}

.title {
  -ms-flex-item-align: start;
      align-self: flex-start;
  font-size: 3rem;
  font-weight: 300;
  color: #333;
  margin-bottom: 30px;
}

.todoInput {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 35px;
