.itemin {
  padding: 15px 15px 15px;
  border-radius: 4px;
  border: 1px solid rgb(238, 238, 238);
  width: 100%;
  margin-bottom: 10px;
}

.clearsearch {
  width : 100%;
  margin-bottom: 10px;
}

.inputz {
  width: 20%;
  margin-right: 25px;
  margin-bottom: 10px;
  padding: 15px 15px 15px;
  border-radius: 4px;
  border: 1px solid rgb(238, 238, 238);
}


.input {
  width: 20%;
  margin-right: 10px;
  padding: 15px 0 15px 15px;
  border-radius: 4px;
  border: 1px solid rgb(238, 238, 238);
}

button {
  background: #62c7ff;
  border: none;
  font-size: 1rem;
  font-weight: 300;
  font-family: "Lato", sans-serif;
  padding: 15px 25px;
  border-radius: 4px;
  color: #fff;
}

.buttonlistitem {
  background: #62c7ff;
  border: none;
  font-size: 0.8rem;
  font-weight: 300;
  font-family: "Lato", sans-serif;
  padding: 7px 15px;
  border-radius: 4px;
  color: #fff;
}

button:hover {
  color: #fff;
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 0 7px 14px rgba(50, 50, 93, .1), 0 3px 6px rgba(0, 0, 0, .08);
          box-shadow: 0 7px 14px rgba(50, 50, 93, .1), 0 3px 6px rgba(0, 0, 0, .08);
