}

ul {
  list-style: none;
  margin-bottom: 50px;
}

li {
  font-weight: 300;
  padding: 5px 0 5px 0;
  border-top: 1px solid #eee;
  display: block;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.mainlist {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow-y: scroll;
  max-height: 250px;
}

.logo-container {
  margin-bottom: 25px;
}

img {
  height: 25px;
}

button.action {
  color: #333;
  background: none;
  padding: 0;
}

button.action:focus {
  outline: none;
}



